import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Table, Select, Button, Badge, Tabs } from 'antd';
import TableLayout from 'Components/TableLayout'
import { toJS } from 'mobx';


@inject('SparepartsStore')
@observer
class DepartmentTable extends Component {
    render() {
        const { tableData, PageInfo, isLoading, columnsType, spareList } = toJS(this.props.SparepartsStore)
        const { TabPane } = Tabs;
        const { Option } = Select;
    console.log(PageInfo)
        //
        const columns = [
            {
                title: '',
                dataIndex: 'spareName',
                key: 'spareName',
            },
            {
                title: '',
                dataIndex: 'model',
                key: 'model',
            },
            {
                title: '',
                dataIndex: 'warnMax',
                key: 'warnMax',
                render: (text, record) => (
                    <div>{record.warnMin ? record.warnMin : 0} - {record.warnMax ? record.warnMax : ''}</div>
                )
            },
            {
                title: '',
                dataIndex: 'stock',
                key: 'stock',
            }
        ];
        return <div /*style={{ marginTop: 20 }}*/>
            <Tabs>
                <TabPane tab={<div><i className="iconfont icon-inventorywarning icon-spacing"></i></div>} key="1" />
            </Tabs>
            <div className="tab-content-bg">
                <Select
                    showSearch
                    allowClear
                    style={{ width: 250 }}
                    optionFilterProp="children"
                    placeholder=''
                    onChange={this.changeSpa}
                    filterOption={(input, option) =>
                        option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    } >
                    {spareList.map(item => <Option key={item.id} value={item.id}>{item.spareName}</Option>)}
                </Select><Button type="primary" style={{ marginLeft: 10 }} onClick={this.onSearchData}  className="btn-border-purple"></Button>
                <Table style={{ marginTop: 10 }} size='middle' rowKey="id" loading={isLoading} onChange={this.pageChange} columns={columns} dataSource={tableData} pagination={PageInfo} onChange={this.pageChange}/>
            </div>
        </div>;
    }
    pageChange=(e)=>{
        this.props.SparepartsStore.PageInfo=e
        this.props.SparepartsStore.PageInfo.pageIndex=e.current
        const { getData, PageInfo, id, getAllSpare } = this.props.SparepartsStore
        getData(3, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, id })
    }
    changeSpa = (e) => {
        this.props.SparepartsStore.id = e
    }
    onSearchData = () => {

        const { getData, PageInfo, id } = this.props.SparepartsStore
        getData(3, { id, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize })
    }
    componentDidMount() {
        const { getData, PageInfo, id, getAllSpare } = this.props.SparepartsStore
        getData(3, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, id })
        getAllSpare()
    }
    componentWillUnmount() {
        this.props.SparepartsStore.clearData()
    }
}
export default DepartmentTable;